<template>
	<el-dialog custom-class="tpublish-dialog" title="发表为模板" :visible.sync="dialogVisible" width="50%" :append-to-body="true">
		<div class="template-publish__body">
			<el-form v-model="template" :label-position="formType.labelPosition">
				<el-form-item label="模板标题">
					<el-input 
						v-model="template.formIssureName"
						minlength="4"
						maxlength="10"
						show-word-limit
					></el-input>
				</el-form-item>
				<el-form-item>
					<span slot='label'><strong>模板标签</strong><br/><i class="templateSort-tip">选择恰当的标签获得更多推荐哦</i></span>
					<div class="templateSort-selects">
						<el-select
							v-for="(item,index) in templteSorts" 
							:key="item.id"
							v-model="template.formIssureTag[index]"
							:placeholder="item.tips"
						>
							<el-option v-for="value in item.values" :key="value" :value="value"></el-option>
						</el-select>
					</div>
				</el-form-item>
				<el-form-item label="模板介绍(选填)">
					<el-input 
						type="textarea" 
						placeholder="简单介绍你的模板用途或特定,更容易通过审核和被用户关注哦~"
						maxlength="500"
						show-word-limit
						v-model="template.formIssureDesc"
						:rows='7'
					>
					</el-input>
				</el-form-item>
				<el-form-item>
					<el-checkbox v-model="agreementChecked"><span class="user-agreement">选中即表示您同意我们的用户协议<i>《用户协议》</i></span></el-checkbox>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" :disabled="!agreementChecked" @click='templateSubmit'>提交</el-button>
					<el-button @click="dialogVisible = false">取消</el-button>
				</el-form-item>
			</el-form>
		</div>
	</el-dialog>
</template>

<script>
	import {TemplatePublish} from '@/network/template.js'
	export default {
		name: 'TemplatePublish',
		data() {
			return {
				dialogVisible: true,
				formType: {
					labelPosition: 'top'
				},
				agreementChecked: true,
				template: {
					formId: this.form.formId,
					formIssureName: this.form.formTitle,
					formIssureTag: ['','',''],
					formIssureDesc: null
				}
			}
		},
		props: ['show', 'form'],
		watch: {
			show: function(newVal) {
				this.dialogVisible = newVal.valueOf();
			}
		},
		computed: {
			templteSorts() {
				return this.$store.getters['templates/getTemplateSorts']
			}
		},
		methods: {
			templateSubmit() {
				let template = JSON.parse(JSON.stringify(this.template))
				let templateTags = template.formIssureTag.filter(item => item != '');
				if(templateTags.length != 0 && templateTags.length != null) {
					template.formIssureTag = templateTags.join(',')
				}else {
					template.formIssureTag = null
				}
				let formData = new FormData();
				formData.append("formId", template.formId)
				formData.append("formIssureName", template.formIssureName)
				formData.append("formIssureTag", template.formIssureTag)
				formData.append("formIssureDesc", template.formIssureDesc)
				TemplatePublish(formData).then(res => {
					console.log(res)
				})
			}
		}
	}
</script>

<style>
	.tpublish-dialog {
		margin-top: 5vh !important;
	}
	.template-publish__body .el-form-item__label {
		line-height: 1.5em !important;
		font-weight: bold;
	}
	.template-publish__body i {
		font-weight: 400;
		font-style: normal;
	}
	.templateSort-tip {
		color: #999;
	}
	.templateSort-selects {
		display: flex;
		justify-content: space-between;
	}
	.user-agreement {
		color: #000 !important;
	}
	.user-agreement>i {
		color: #2875e8;
	}
</style>
