import {
	request
} from './request'

/**
 * @param {Object} data
 * 表单发布为模板
 */
export function TemplatePublish(data) {
	return request({
		method: 'PATCH',
		url: '/IssureFormController/updateFormIssure',
		data
	})
}

// 模拟请求template 标签 数据
export function getTemplateTags() {
	return request({
		url: '/data/template.json'
	})
}

export function getTemplateList() {
	return request({
		url: '/data/temlateList.json'
	})
}
